.slick-list2 .slick-slider {
    display: block;
    position: relative;
    box-sizing: border-box;
    user-select: none;
    width: 65% !important;
    height: fit-content;
    height: 300px !important;
}

.slick-list2 {
    position: relative;
    display: block;
    overflow: hidden;
    padding: 0;
    margin: 0;
    height: fit-content;
    width: 100%;
}

.slick-list2 .separate-slider__img-figure {
    height: 60%;
    padding-left: 20px;
    width: 80%;
    padding-top: 18px;
}

/* .slick-slider .slick-list, .slick-slider .slick-track {
    transform: translateZ(0);
    height: 700px;
} */

.slick-list2 .separate-slider__content {
    display: flex;
    flex-direction: column;
    padding: 24px 15px 20px;
    background: #fff;
    /* border-radius: 8px; */
    /* box-shadow: 15px 15px 15px rgba(69, 60, 144, .2),0 4px 5px rgba(69, 60, 144, .12),0 2px 4px rgba(69, 60, 144, .14); */
    /* box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15); */
    line-height: 27px;
    height: fit-content;
    margin-top: 20px;
    text-align: start;
}

@media (max-width: 640px) {
    .separate-slider__content {
        height: fit-content;
    }

    .separate-slider__content p {
        /* display: none; */
    }

    .separate-slider__title {
        font-size: 16px;
    }
}

@media (max-width: 928px) {
    .separate-slider__content {
        height: fit-content;
    }

    .separate-slider__content p {
        /* display: none; */
    }
}

@media (max-width: 1334px) {
    .separate-slider__content {
        height: fit-content;
    }
}

.slick-list2 .separate-slider__item {
    display: flex !important;
    height: max-content;
    flex-direction: row-reverse;
    /* max-width: 334px; */
    /* margin: 20px; */
    align-items: center;
}

.slick-slide img {
    display: block;
    width: 100%;
    border-radius: 8px;
    min-height: 220px;
    object-fit: cover;
    height: 220px;
}

.separate-slider__title {
    font-size: 22px;
    margin-bottom: 20px;
}

.separate-slider__text b {
    margin-bottom: 10px;
    font-weight: 500;
}

.target-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: max-content;
    align-items: center;
    padding-top: 0px;
}

@media (max-width: 1100px) {
    .separate-slider__img-figure {
        display: none;
    }

    .separate-slider__content {
        background-color: transparent !important;
    }

    @media (min-width: 570px) {
        .item_inner {
            box-shadow: 5px 10px 12px #8080804f;
            /* margin: 20px; */
            height: 100% !important;
            padding: 20px;
        }

        .slick-list2 .separate-slider__item {
            padding: 20px;
        }

        .slick-slider .slick-track {
            height: 100% !important;
        }
    }
}

@media(min-width: 1100px) {
    .separate-slider__content {
        background-image: none;

    }

}