body, html{
    margin: 0;
    padding: 0;
}
body {
    color: #141414;
    font-family: 'proxima-nova', 'Helvetica Neue', Arial, sans-serif;
    font-size: 16px;
    /* font-weight: 300; */
    line-height: 1.25;
}

*, :after, :before {
    box-sizing: inherit;
}

.toolinformation {
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: #453c90;
    height: 690px;
    animation: h 1s 2s forwards;
    background-position: 50% 100%;
    background-repeat: no-repeat;
    background-size: 972px auto;
    bottom: 0;
    content: "";
    left: 0;
    right: 0;
    top: 0;
    background-image: url(../virtual-assistant-bg.svg);
}


.grids{
    padding-top: 100px;
}

.top {
    max-width: 100%;
    height:500px;
    margin: 0;
    text-align: center;
    
    justify-content: center;
}
.heading{
    padding-top: 150px;
    text-align: center;
    font-weight: 600;
    color: #fff;
    font-size: 35px;
    font-family: cabin,Helvetica Neue,Arial,sans-serif;
}
.heading_inner{
    display: flex;
    text-align: center;
    justify-content: center;
    padding-top: 20px;
    max-width: 100%;
    font-weight: 400;
    color: #fff;
}
.heading_inner p{
    max-width: 690px;
}
.buttons{
    display: flex;
    text-align: center;
    justify-content: center;
}
.buttons .get_guid{
    border-color: #51a9e1;
    background-color: #51a9e1;
    border-radius: 4px;
    padding: 20px;
    font-weight: 400;
    color: #fff;
}
.buttons .freechat{
    margin-left: 20px;
    padding: 20px;
    border-color: #ce7d00;
    background-color: #e68c00;
    border-radius: 4px;
    font-weight: 400;
    color: #fff;
}
.get_guid, .freechat .button{
    font-weight: 400;
    color: #fff;
}

.video{
    margin-top: 100px;
    text-align: center;
}
.video h2{
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
    margin-bottom: 20px;
}

.head-block-center{
    margin-bottom: 0px;
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
}
.headblock{
    margin-bottom: 0px;
    padding-bottom: 0px;
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
}
.text-block-center{
    margin-top: 0px;
    /* font-weight: 300; */
}

.section-text-image{
    margin-top: 50px;
}

.section-text-image-inner{
    display: flex;
    justify-content: center;
}
.setion-text-image__details{
    margin: 10px;
    width: 500px;
    height: auto;
    display: grid;
  place-items: center;
}
.section-text-image__title{
    font-size: 24px;
    font-weight: 500;
}

.section-text-image__figure{
    margin: 10px;
}
.section-text-image__figure img{
    width: 400px;
}
.section-text-image__description{
    font-size: 16px;
    line-height: 27px;
    /* font-weight: 300; */
    
}
.industries__inner{
    display: flex;
    justify-content: center;
    margin-top: 100px;
    margin-bottom: 20px;
}

.industries__list {
    -ms-flex-line-pack: stretch;
    align-content: stretch;
    -ms-flex-align: stretch;
    align-items: stretch;
    margin: 35px auto 0;
    /* margin-bottom: 20px; */
}
.industries__list {
    display: -ms-flexbox;
    display: flex;
    max-width: 832px;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-align: start;
    align-items: flex-start;
    -ms-flex-pack: justify;
    justify-content: space-between;
    margin: 0 auto;
}

.industries__item {
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 1px 10px #453c9033, 0 4px 5px #453c901f, 0 2px 4px #453c9024;
    height: 100%;
    margin-bottom: 32px;
    padding: 44px 20px 32px;
    text-align: center;
    transition: box-shadow .2s;
    width: 256px;
}
.industries__list-item{
    height: 256px;
    margin: 10px;
}

.industries__item {
    width: 256px;
    padding: 44px 20px 32px;
    margin-bottom: 32px;
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 1px 10px #453c9033, 0 4px 5px #453c901f, 0 2px 4px #453c9024;
    text-align: center;
}
.industries__item-icon{
    width: 104px;
    height: 90px;
    margin: 0 auto 10px;
}
.industries__item-title{
    font-size: 16px;
    font-weight: 600;
}



